.footerBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 50vh;
    padding-bottom: 20px;
    background-color: #710783;
}

.title {
    margin: 20px;
    color: white;
}

.link {
    padding: 20px;
    color: white;
    text-decoration: none;
}

.container {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    display: flex;
}

.linksBlock {
    gap: 35px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
